// lib/api/index.ts

import axios from 'axios';
import type { AxiosResponse } from 'axios';
import type {
  Note,
  CreateNotePayload,
  FetchNotesParams,
  FetchNotesResponse,
} from '@/types/note'; // Абсолютний імпорт

// Створюємо екземпляр axios з базовим URL
const apiClient = axios.create({
  baseURL: 'https://notehub-public.goit.study/api',
});

// Interceptor для автоматичного додавання токена авторизації
apiClient.interceptors.request.use(config => {
  // Отримуємо токен з process.env для Next.js
  const token = process.env.NEXT_PUBLIC_NOTEHUB_TOKEN;
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Функція для отримання списку нотаток з пагінацією та пошуком
export const fetchNotes = async (
  params: FetchNotesParams = {}
): Promise<FetchNotesResponse> => {
  const response: AxiosResponse<FetchNotesResponse> = await apiClient.get(
    '/notes',
    {
      params: {
        page: params.page || 1,
        search: params.search || '',
        perPage: params.perPage || 12,
      },
    }
  );
  return response.data;
};

// Функція для отримання однієї нотатки за її ID (потрібна для динамічної сторінки)
export const fetchNoteById = async (noteId: string): Promise<Note> => {
  const response: AxiosResponse<Note> = await apiClient.get(`/notes/${noteId}`);
  return response.data;
};

// Функція для створення нової нотатки
export const createNote = async (noteData: CreateNotePayload): Promise<Note> => {
  const response: AxiosResponse<Note> = await apiClient.post('/notes', noteData);
  return response.data;
};

// Функція для видалення нотатки
export const deleteNote = async (noteId: string): Promise<Note> => {
  const response: AxiosResponse<Note> = await apiClient.delete(
    `/notes/${noteId}`
  );
  return response.data;
};